/* SOLAR SYSTEM  */
.solar-container {
    min-width: 98vw;
    min-height: 93vh;
    position: relative;
    z-index: 120;
    background-color: #080725;
    background: rgb(34,33,71);
    background: radial-gradient(circle, rgba(34,33,71,0.9657329484528187) 0%, rgba(22,23,51,1) 37%, rgba(3,1,18,1) 100%); 
}

.solar-container h2 {
    position: absolute;
    top: 25%;
}

/* ORBITS */
.orbit-container {
    width: 70vw;
    height: 55rem;
    margin: 0 auto;
    position: relative;
    top: 5rem;
}

.orbit {
    position: absolute;
    border-radius: 100%;
    border: solid 1px rgba(137, 196, 244, 0.1);
    transition: .2s;
}
  
/* PLANETS */
#sun {
    position: absolute;
    top: calc(320px - 40px);
    left: calc(50% - 40.2px);
    width: 86.4340px;
    height: 86.4340px;
    background: linear-gradient(#fcd670, #f2784b);
    border-radius: 100%;
    box-shadow: 0px 0px 8px 8px rgba(242, 120, 75, 0.2);
    transition: .3s;
}

