    border: dashed 1px rgba(137, 196, 244, 0.1);
    -webkit-animation: spin-right 1240s linear infinite;
    animation: spin-right 1240s linear infinite;
}

#pluto {
    position: absolute;
    width: 4px;
    height: 4px;
    top: 330px;
    border-radius: 100%;
    background: linear-gradient(silver, #fafafa);
}


#planet-list li {
    margin: 10px;
    cursor: pointer;
}

#planet-list li a {
    text-decoration: none;
    color: white;
}

@-webkit-keyframes spin-right {
    100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
    }
}

@keyframes spin-right {
    100% {
        -webkit-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
